"use client";

import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

// バリデーションスキーマ
export const aspFormSchema = z.object({
  name: z.string().min(1, { message: "ASP名は必須です。" }),
  login_url: z.string().url({ message: "有効なURLを入力してください。" }).optional().or(z.literal('')),
  category: z.string().optional(),
  prompt: z.string().optional(),
});

type AspFormValues = z.infer<typeof aspFormSchema>;

interface AspFormProps {
  // TODO: 編集時のデフォルト値や、保存処理の関数をPropsで受け取る
  onSubmit: (values: AspFormValues) => void;
  isPending: boolean;
}

export function AspForm({ onSubmit, isPending }: AspFormProps) {
  const form = useForm<AspFormValues>({
    resolver: zodResolver(aspFormSchema),
    // TODO: デフォルト値を設定
    defaultValues: {
      name: "",
      login_url: "",
      category: "",
      prompt: "",
    },
  });

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
        <FormField
          control={form.control}
          name="name"
          render={({ field }) => (
            <FormItem>
              <FormLabel>ASP名</FormLabel>
              <FormControl>
                <Input placeholder="A8.net" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="login_url"
          render={({ field }) => (
            <FormItem>
              <FormLabel>ログインURL</FormLabel>
              <FormControl>
                <Input placeholder="https://www.a8.net/" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="category"
          render={({ field }) => (
            <FormItem>
              <FormLabel>カテゴリ</FormLabel>
              <Select onValueChange={field.onChange} defaultValue={field.value}>
                <FormControl>
                  <SelectTrigger>
                    <SelectValue placeholder="カテゴリを選択" />
                  </SelectTrigger>
                </FormControl>
                <SelectContent>
                  <SelectItem value="ASP">ASP</SelectItem>
                  <SelectItem value="SaaS">SaaS</SelectItem>
                  <SelectItem value="Other">その他</SelectItem>
                </SelectContent>
              </Select>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="prompt"
          render={({ field }) => (
            <FormItem>
              <FormLabel>プロンプト</FormLabel>
              <FormControl>
                <Textarea
                  placeholder="1. ID入力欄に...\n2. パスワード入力欄に..."
                  className="min-h-[200px]"
                  {...field}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <div className="flex justify-end">
          <Button type="submit" disabled={isPending}>
            {isPending ? "保存中..." : "保存"}
          </Button>
        </div>
      </form>
    </Form>
  );
}
